import React from 'react'
import { Controller } from 'react-hook-form'
import SelectDropdown from 'components/SelectDropdown/SelectDropdown'
import Input from 'components/Input/Input'
import Datepicker from 'components/Datepicker/Datepicker'

const useRenderFormItems = ({list, control}) => {
  const renderFormItems = () => list.map(({type, validation, ...item}, index) => {
    switch (type) {
      case 'select':
        return (
          <Controller
            as={SelectDropdown}
            control={control}
            rules={{
              ...validation
            }}
            key={index}
            {...item}
          />
        )
      case 'inputCash':
        return (
          <Controller
            as={Input}
            control={control}
            rules={{
              ...validation
            }}
            key={index}
            isCash
            {...item}
          />
        )
      case 'datepicker':
        // return (
        //   <Controller
        //     as={Datepicker}
        //     control={control}
        //     key={index}
        //     {...item}
        //   />
        // )
      default:
        return (
          <Controller
            as={Input}
            control={control}
            rules={{
              ...validation
            }}
            key={index}
            {...item}
          />
        )
    }
  })

  return renderFormItems()
}

export default useRenderFormItems
