import React, { useCallback, useEffect, useRef, useState } from 'react'
import css from './Datepicker.module.scss'
import classnames from 'classnames'
import PropTypes from 'prop-types'
// datepicker stuff
import DayPicker, { DateUtils } from 'react-day-picker'
import 'react-day-picker/lib/style.css'
import IconCalendar from 'assets/icons/IconCalendar'
import { addZeroToSingleDigit } from 'utils'
import { useDispatch, useSelector } from 'react-redux'

const Datepicker = ({
  className,
  defaultDate,
  isSingleDaySelection,
  onDateSelect = () => {},
}) => {
  const localization = {
    locale: 'ru',
    weekdaysShort: [
      'ВСК',
      'ПНД',
      'ВТР',
      'СРД',
      'ЧТВ',
      'ПТН',
      'СБТ'
    ],
    months: [
      'Январь',
      'Февраль',
      'Март',
      'Апрель',
      'Май',
      'Июнь',
      'Июль',
      'Август',
      'Сентябрь',
      'Октябрь',
      'Ноябрь',
      'Декабрь',
    ],
  }

  const calendarData = useSelector(state => state.data.calendar)
  const dispatch = useDispatch()
  const [calendarState, updateCalendarState] = useState({
    from: null,
    to: null,
    enteredTo: null
  })
  const [isCalendarOpen, setCalendarOpen] = useState(false)
  const calendarWrapperRef = useRef(null)
  const buttonApplyRef = useRef(null)

  const isSelectingFirstDay = (from, to, day) => {
    const isBeforeFirstDay = from && DateUtils.isDayBefore(day, from);
    const isRangeSelected = from && to;
    return !from || isBeforeFirstDay || isRangeSelected;
  }

  const handleDayClick = day => {
    const { from, to } = calendarState;
    if (!isSingleDaySelection && from && to && day >= from && day <= to) {
      const date = defaultDate || null
      updateCalendarState({
        from: date,
        to: date,
        enteredTo: date
      })
      return;
    }
    if (isSingleDaySelection) {
      updateCalendarState({
        from: day,
        to: day,
        enteredTo: null
      })
      // dispatch(setCalendarData(day, day, null))
    } else if (isSelectingFirstDay(from, to, day)) {
      updateCalendarState({
        from: day,
        to: null,
        enteredTo: null
      })
      // dispatch(setCalendarData(day, null, null))
    } else {
      updateCalendarState({
        from: from,
        to: day,
        enteredTo: day
      })
      // dispatch(setCalendarData(from, day, day))
    }
  }

  const handleResetClick = () => {
    const date = defaultDate || null
    // dispatch(resetCalendarFilter(date, date, null))
    updateCalendarState({
      from: date,
      to: date,
      enteredTo: date
    })
  }

  const handleCloseCalendar = () => {
    setCalendarOpen(false)
  }

  const handleToggleCalendar = () => {
    setCalendarOpen(state => !state)
  }

  useEffect(() => {
    if (isSingleDaySelection && !calendarState.from) {
      const date = defaultDate || new Date()
      updateCalendarState({
        from: date,
        to: date,
        enteredTo: null
      })
      // dispatch(setCalendarData(date, date, null))
      // dispatch(applyFiltration())
    }
  }, [dispatch, isSingleDaySelection, defaultDate, calendarState.from])

  useEffect(() => {
    if (defaultDate) {
      // dispatch(setCalendarData(defaultDate, defaultDate, null))
    }
  }, [defaultDate, dispatch])

  const handleClickOutside = useCallback(evt => {
    if (!calendarWrapperRef.current.contains(evt.target) && buttonApplyRef.current) {
      updateCalendarState(calendarData)
      handleCloseCalendar()
    }
  }, [calendarData])

  useEffect(() => {
    document.addEventListener('click', handleClickOutside)

    return () => {
      document.removeEventListener('click', handleClickOutside)
    }
  }, [handleClickOutside])

  const { from, to, enteredTo } = calendarState
  const modifiers = { start: from, end: enteredTo }
  const disabledDays = { before: calendarData.from }
  const selectedDays = [from, { from, to: enteredTo }]

  let labelContent = isSingleDaySelection ? 'Выберите дату' : 'Выбор интервала дат'

  if ((!isSingleDaySelection && calendarData.from && !calendarData.to) || (isSingleDaySelection && calendarData.from)) {
    labelContent = `${addZeroToSingleDigit(calendarData.from.getDate())}.${addZeroToSingleDigit(calendarData.from.getMonth() + 1)}.${calendarData.from.getFullYear()}`
  }

  if (!isSingleDaySelection && calendarData.from && calendarData.to) {
    const fromLocal = `${addZeroToSingleDigit(calendarData.from.getDate())}.${addZeroToSingleDigit(calendarData.from.getMonth() + 1)}.${calendarData.from.getFullYear()}`
    const toLocal = `${addZeroToSingleDigit(calendarData.to.getDate())}.${addZeroToSingleDigit(calendarData.to.getMonth() + 1)}.${calendarData.to.getFullYear()}`
    labelContent = `с ${fromLocal} по ${toLocal}`
  }

  return (
    <div className={classnames(css.wrapper, className)} ref={calendarWrapperRef}>
      <div
        className={css.label}
        onClick={handleToggleCalendar}
      >
        {!isSingleDaySelection && calendarData.from && !calendarData.to &&
          <span className={css.hint}>
            Выберите конечную дату
          </span>
        }
        <span className={classnames(css.labelContent, { [css.labelContentActive]: !!calendarData.from })}>
          { labelContent }
        </span>
        <IconCalendar className={css.iconCalendar} />
      </div>
      {isCalendarOpen &&
        <div className={css.content}>
          <DayPicker
            className={css.datepicker}
            numberOfMonths={1}
            selectedDays={selectedDays}
            disabledDays={isSingleDaySelection ? null : disabledDays}
            modifiers={modifiers}
            onDayClick={handleDayClick}
            locale={localization.locale}
            weekdaysShort={localization.weekdaysShort}
            months={localization.months}
            month={calendarState.from ? calendarState.from : new Date()}
            firstDayOfWeek={1}
          />
          <div className={css.controls}>
            <button
              className={classnames(css.btnControl, css.btnControlApply)}
              onClick={() => {
                // dispatch(setCalendarData(from, to, enteredTo))
                // dispatch(applyFiltration())
                onDateSelect(calendarState)
                handleCloseCalendar()
              }}
              ref={buttonApplyRef}
            >
              Применить
            </button>
            <button
              className={classnames(css.btnControl, css.btnControlReset)}
              onClick={() => {
                handleResetClick()
                handleCloseCalendar()
              }}
            >
              Отменить
            </button>
          </div>
        </div>
      }
    </div>
  )
}

Datepicker.propTypes = {
  className: PropTypes.string
}

export default React.memo(Datepicker)
